import "../styles/Footer.css"

function footer() {
  return (
    <div className = "footer">
        <div className = "socialMedia">
            <GitHubIcon />
            <LinkedInIcon />
        </div>
        <p>&copy; 2022 coreycullen.ie</p>
    </div>
  );
}

export default footer;